// Grid
$row-width: 120rem !default;
$max-width: 80%;

//Colors
$black: #000;
$white: #fff;
$grey: #999;

//Typefaces
$arial: 'Arial', sans-serif;

//Global
$primary-font: $arial;
$primary-font-color: $grey;
$primary-font-size: 16px;
$primary-font-weight: normal;
$primary-link-color: $black;

//Font faces
$font-name: 'roboto';
$font-primary--light: '#{$font-name}-light-webfont', Helvetica, Arial, sans-serif;
$font-primary--regular: '#{$font-name}-regular-webfont', Helvetica, Arial, sans-serif;
$font-primary--medium: '#{$font-name}-medium-webfont', Helvetica, Arial, sans-serif;
$font-primary--bold: '#{$font-name}-bold-webfont', Helvetica, Arial, sans-serif;

//Breakpoints
$small: 540px;
$medium: 768px;
$large: 1024px;
$x-large: 1280px;
$xx-large: 1600px;

//Spacing
$margin-default: 4rem;

//Hamburger menu
$button-width: 18px;                    // The width of the button area
$button-height: 18px;                   // The height of the button area
$bar-thickness: 2px;                    // The thickness of the button bars
$button-pad: 0;                      // The left/right padding between button area and bars.
$button-bar-space: 4px;                // The spacing between button bars
$button-transistion-duration: .3s;     // The transition duration
